"use client"

import { useState } from "react"
import { v4 as uuidv4 } from "uuid"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Label } from "@/components/ui/label"
import { Slider } from "@/components/ui/slider"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import { PatternLayer, PatternShape } from "@/lib/patternGenerator"

interface PatternControlsProps {
  onUpdate: (layers: PatternLayer[]) => void
  onDownload: () => void
}

export function PatternControls({ onUpdate, onDownload }: PatternControlsProps) {
  const [layers, setLayers] = useState<PatternLayer[]>([
    {
      id: uuidv4(),
      shape: {
        type: 'circle',
        size: 20,
        rotation: 0,
        spacing: 40,
        opacity: 1
      },
      colors: ['#6d28d9', '#ffffff'],
      blendMode: 'source-over',
      opacity: 1,
      scale: 1,
      rotation: 0,
      offset: { x: 0, y: 0 }
    }
  ])

  const [selectedLayerId, setSelectedLayerId] = useState<string>(layers[0].id)

  const handleAddLayer = () => {
    const newLayer: PatternLayer = {
      id: uuidv4(),
      shape: {
        type: 'circle',
        size: 20,
        rotation: 0,
        spacing: 40,
        opacity: 1
      },
      colors: ['#6d28d9', '#ffffff'],
      blendMode: 'source-over',
      opacity: 1,
      scale: 1,
      rotation: 0,
      offset: { x: 0, y: 0 }
    }
    setLayers([...layers, newLayer])
    setSelectedLayerId(newLayer.id)
    onUpdate([...layers, newLayer])
  }

  const handleRemoveLayer = (id: string) => {
    if (layers.length <= 1) return // Keep at least one layer
    const newLayers = layers.filter(layer => layer.id !== id)
    setLayers(newLayers)
    setSelectedLayerId(newLayers[0].id)
    onUpdate(newLayers)
  }

  const handleLayerUpdate = (id: string, updates: Partial<PatternLayer>) => {
    const newLayers = layers.map(layer =>
      layer.id === id ? { ...layer, ...updates } : layer
    )
    setLayers(newLayers)
    onUpdate(newLayers)
  }

  const selectedLayer = layers.find(layer => layer.id === selectedLayerId)
  if (!selectedLayer) return null

  return (
    <Card className="p-6 space-y-6">
      <div className="space-y-4">
        {/* Layer Management */}
        <div className="flex justify-between items-center">
          <Label>Layers</Label>
          <Button
            variant="outline"
            size="sm"
            onClick={handleAddLayer}
          >
            Add Layer
          </Button>
        </div>

        <div className="space-y-2">
          {layers.map(layer => (
            <div
              key={layer.id}
              className={`p-2 border rounded-md cursor-pointer flex justify-between items-center ${
                layer.id === selectedLayerId ? 'bg-purple-50 border-purple-200' : ''
              }`}
              onClick={() => setSelectedLayerId(layer.id)}
            >
              <div className="flex items-center space-x-2">
                <div
                  className="w-4 h-4 rounded-full"
                  style={{ backgroundColor: layer.colors[0] }}
                />
                <span>Layer {layers.indexOf(layer) + 1}</span>
              </div>
              {layers.length > 1 && (
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={(e) => {
                    e.stopPropagation()
                    handleRemoveLayer(layer.id)
                  }}
                >
                  Remove
                </Button>
              )}
            </div>
          ))}
        </div>

        {/* Shape Settings */}
        <div className="space-y-2">
          <Label>Shape Type</Label>
          <Select
            value={selectedLayer.shape.type}
            onValueChange={(value) =>
              handleLayerUpdate(selectedLayerId, {
                shape: { ...selectedLayer.shape, type: value as PatternShape['type'] }
              })
            }
          >
            <SelectTrigger>
              <SelectValue placeholder="Select shape type" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="circle">Circle</SelectItem>
              <SelectItem value="square">Square</SelectItem>
              <SelectItem value="triangle">Triangle</SelectItem>
              <SelectItem value="hexagon">Hexagon</SelectItem>
            </SelectContent>
          </Select>
        </div>

        {/* Colors */}
        <div className="space-y-2">
          <Label>Colors</Label>
          <div className="flex items-center space-x-2">
            <input
              type="color"
              value={selectedLayer.colors[0]}
              onChange={(e) =>
                handleLayerUpdate(selectedLayerId, {
                  colors: [e.target.value, selectedLayer.colors[1]]
                })
              }
              className="w-10 h-10 rounded cursor-pointer"
            />
            <input
              type="color"
              value={selectedLayer.colors[1]}
              onChange={(e) =>
                handleLayerUpdate(selectedLayerId, {
                  colors: [selectedLayer.colors[0], e.target.value]
                })
              }
              className="w-10 h-10 rounded cursor-pointer"
            />
          </div>
        </div>

        {/* Blend Mode */}
        <div className="space-y-2">
          <Label>Blend Mode</Label>
          <Select
            value={selectedLayer.blendMode}
            onValueChange={(value) =>
              handleLayerUpdate(selectedLayerId, {
                blendMode: value as GlobalCompositeOperation
              })
            }
          >
            <SelectTrigger>
              <SelectValue placeholder="Select blend mode" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="source-over">Normal</SelectItem>
              <SelectItem value="multiply">Multiply</SelectItem>
              <SelectItem value="screen">Screen</SelectItem>
              <SelectItem value="overlay">Overlay</SelectItem>
              <SelectItem value="darken">Darken</SelectItem>
              <SelectItem value="lighten">Lighten</SelectItem>
            </SelectContent>
          </Select>
        </div>

        {/* Shape Size */}
        <div className="space-y-2">
          <Label>Size</Label>
          <Slider
            value={[selectedLayer.shape.size]}
            onValueChange={(value) =>
              handleLayerUpdate(selectedLayerId, {
                shape: { ...selectedLayer.shape, size: value[0] }
              })
            }
            min={5}
            max={100}
            step={1}
          />
        </div>

        {/* Shape Spacing */}
        <div className="space-y-2">
          <Label>Spacing</Label>
          <Slider
            value={[selectedLayer.shape.spacing]}
            onValueChange={(value) =>
              handleLayerUpdate(selectedLayerId, {
                shape: { ...selectedLayer.shape, spacing: value[0] }
              })
            }
            min={10}
            max={200}
            step={1}
          />
        </div>

        {/* Layer Opacity */}
        <div className="space-y-2">
          <Label>Opacity</Label>
          <Slider
            value={[selectedLayer.opacity * 100]}
            onValueChange={(value) =>
              handleLayerUpdate(selectedLayerId, {
                opacity: value[0] / 100
              })
            }
            min={0}
            max={100}
            step={1}
          />
        </div>

        {/* Layer Rotation */}
        <div className="space-y-2">
          <Label>Rotation</Label>
          <Slider
            value={[selectedLayer.rotation]}
            onValueChange={(value) =>
              handleLayerUpdate(selectedLayerId, {
                rotation: value[0]
              })
            }
            min={0}
            max={360}
            step={1}
          />
        </div>
      </div>

      <div className="flex flex-col space-y-2">
        <Button
          onClick={onDownload}
          className="w-full bg-purple-600 hover:bg-purple-700 text-white"
        >
          Download Pattern
        </Button>
        <Button
          onClick={() => {
            const defaultLayer: PatternLayer = {
              id: uuidv4(),
              shape: {
                type: 'circle',
                size: 20,
                rotation: 0,
                spacing: 40,
                opacity: 1
              },
              colors: ['#6d28d9', '#ffffff'],
              blendMode: 'source-over',
              opacity: 1,
              scale: 1,
              rotation: 0,
              offset: { x: 0, y: 0 }
            }
            setLayers([defaultLayer])
            setSelectedLayerId(defaultLayer.id)
            onUpdate([defaultLayer])
          }}
          variant="outline"
          className="w-full"
        >
          Reset
        </Button>
      </div>
    </Card>
  )
}
