export interface PatternShape {
  type: 'circle' | 'square' | 'triangle' | 'hexagon' | 'custom'
  size: number
  rotation: number
  spacing: number
  opacity: number
}

export interface PatternLayer {
  id: string
  shape: PatternShape
  colors: string[]
  blendMode: GlobalCompositeOperation
  opacity: number
  scale: number
  rotation: number
  offset: { x: number; y: number }
  imageData?: ImageData | null
}

export class PatternGenerator {
  private canvas: HTMLCanvasElement
  private ctx: CanvasRenderingContext2D

  constructor(canvas: HTMLCanvasElement) {
    this.canvas = canvas
    const context = canvas.getContext('2d')
    if (!context) throw new Error('Could not get canvas context')
    this.ctx = context
  }

  public async generatePattern(layers: PatternLayer[]): Promise<void> {
    console.log('Generating pattern with layers:', layers)
    console.log('Canvas dimensions:', this.canvas.width, 'x', this.canvas.height)
    
    // Clear the entire canvas
    this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height)

    // Draw debug background grid
    this.drawDebugGrid()

    for (const layer of layers) {
      console.log('Drawing layer:', layer)
      await this.drawLayer(layer)
    }
  }

  private drawDebugGrid(): void {
    // Save current context state
    this.ctx.save()

    // Set grid style
    this.ctx.strokeStyle = '#eee'
    this.ctx.lineWidth = 0.5

    // Draw vertical lines
    for (let x = 0; x < this.canvas.width; x += 50) {
      this.ctx.beginPath()
      this.ctx.moveTo(x, 0)
      this.ctx.lineTo(x, this.canvas.height)
      this.ctx.stroke()
    }

    // Draw horizontal lines
    for (let y = 0; y < this.canvas.height; y += 50) {
      this.ctx.beginPath()
      this.ctx.moveTo(0, y)
      this.ctx.lineTo(this.canvas.width, y)
      this.ctx.stroke()
    }

    // Restore context state
    this.ctx.restore()
  }

  private async drawLayer(layer: PatternLayer): Promise<void> {
    console.log('Drawing layer with settings:', {
      shape: layer.shape,
      colors: layer.colors,
      blendMode: layer.blendMode,
      opacity: layer.opacity,
      scale: layer.scale,
      rotation: layer.rotation,
      offset: layer.offset
    })

    const { shape, colors, blendMode, opacity, scale, rotation, offset } = layer

    // Create a temporary canvas for the layer
    const tempCanvas = document.createElement('canvas')
    tempCanvas.width = this.canvas.width
    tempCanvas.height = this.canvas.height
    const tempCtx = tempCanvas.getContext('2d')
    if (!tempCtx) throw new Error('Could not get temporary canvas context')

    console.log('Temporary canvas created with dimensions:', tempCanvas.width, 'x', tempCanvas.height)

    // Set blend mode and opacity
    tempCtx.globalCompositeOperation = blendMode
    tempCtx.globalAlpha = opacity

    try {
      if (layer.imageData) {
        console.log('Drawing image pattern')
        await this.drawImagePattern(tempCtx, layer)
      } else {
        console.log('Drawing shape pattern:', shape.type)
        this.drawShapePattern(tempCtx, layer)
      }

      // Apply transformations to the main canvas
      this.ctx.save()
      this.ctx.translate(offset.x, offset.y)
      this.ctx.rotate((rotation * Math.PI) / 180)
      this.ctx.scale(scale, scale)
      this.ctx.drawImage(tempCanvas, 0, 0)
      this.ctx.restore()

      console.log('Layer drawn successfully')
    } catch (error) {
      console.error('Error drawing layer:', error)
      throw error
    }
  }

  private drawShapePattern(ctx: CanvasRenderingContext2D, layer: PatternLayer): void {
    const { shape, colors } = layer
    const size = Math.max(20, shape.size) // Ensure minimum size
    const spacing = Math.max(40, shape.spacing) // Ensure minimum spacing

    console.log('Drawing shape pattern with:', {
      shapeType: shape.type,
      size,
      spacing,
      colors,
      canvasSize: `${this.canvas.width}x${this.canvas.height}`
    })

    // Clear canvas and fill background
    ctx.clearRect(0, 0, this.canvas.width, this.canvas.height)
    if (colors[1]) {
      ctx.fillStyle = colors[1]
      ctx.fillRect(0, 0, this.canvas.width, this.canvas.height)
    }

    // Draw debug grid
    ctx.strokeStyle = '#ddd'
    ctx.lineWidth = 1
    for (let x = 0; x < this.canvas.width; x += 50) {
      ctx.beginPath()
      ctx.moveTo(x, 0)
      ctx.lineTo(x, this.canvas.height)
      ctx.stroke()
    }
    for (let y = 0; y < this.canvas.height; y += 50) {
      ctx.beginPath()
      ctx.moveTo(0, y)
      ctx.lineTo(this.canvas.width, y)
      ctx.stroke()
    }

    // Draw shapes
    const offset = spacing / 2
    for (let x = offset; x < this.canvas.width + size; x += spacing) {
      for (let y = offset; y < this.canvas.height + size; y += spacing) {
        ctx.save()
        ctx.translate(x, y)
        ctx.rotate((shape.rotation * Math.PI) / 180)
        
        const colorIndex = Math.floor((x + y) / spacing) % colors.length
        ctx.fillStyle = colors[colorIndex]

        try {
          switch (shape.type) {
            case 'circle':
              this.drawCircle(ctx, size)
              break
            case 'square':
              this.drawSquare(ctx, size)
              break
            case 'triangle':
              this.drawTriangle(ctx, size)
              break
            case 'hexagon':
              this.drawHexagon(ctx, size)
              break
            default:
              console.warn('Unknown shape type:', shape.type)
              this.drawCircle(ctx, size)
          }
        } catch (error) {
          console.error('Error drawing shape:', error)
        }
        
        ctx.restore()
      }
    }

    console.log('Shape pattern drawing completed')
  }

  private drawCircle(ctx: CanvasRenderingContext2D, size: number): void {
    const radius = size / 2
    ctx.beginPath()
    ctx.arc(0, 0, radius, 0, Math.PI * 2)
    ctx.fill()
    ctx.lineWidth = 2
    ctx.strokeStyle = '#000'
    ctx.stroke()
  }

  private drawSquare(ctx: CanvasRenderingContext2D, size: number): void {
    const half = size / 2
    ctx.beginPath()
    ctx.rect(-half, -half, size, size)
    ctx.fill()
    ctx.lineWidth = 2
    ctx.strokeStyle = '#000'
    ctx.stroke()
  }

  private drawTriangle(ctx: CanvasRenderingContext2D, size: number): void {
    const height = (size * Math.sqrt(3)) / 2
    ctx.beginPath()
    ctx.moveTo(0, -height / 2)
    ctx.lineTo(size / 2, height / 2)
    ctx.lineTo(-size / 2, height / 2)
    ctx.closePath()
    ctx.fill()
    ctx.lineWidth = 2
    ctx.strokeStyle = '#000'
    ctx.stroke()
  }

  private drawHexagon(ctx: CanvasRenderingContext2D, size: number): void {
    ctx.beginPath()
    for (let i = 0; i < 6; i++) {
      const angle = (i * Math.PI) / 3
      const x = size * Math.cos(angle)
      const y = size * Math.sin(angle)
      if (i === 0) ctx.moveTo(x, y)
      else ctx.lineTo(x, y)
    }
    ctx.closePath()
    ctx.fill()
    ctx.lineWidth = 2
    ctx.strokeStyle = '#000'
    ctx.stroke()
  }

  private async drawImagePattern(
    ctx: CanvasRenderingContext2D,
    layer: PatternLayer
  ): Promise<void> {
    if (!layer.imageData) return

    const pattern = ctx.createPattern(await this.imageDataToCanvas(layer.imageData), 'repeat')
    if (!pattern) return

    ctx.fillStyle = pattern
    ctx.fillRect(0, 0, this.canvas.width, this.canvas.height)
  }

  private async imageDataToCanvas(imageData: ImageData): Promise<HTMLCanvasElement> {
    const canvas = document.createElement('canvas')
    canvas.width = imageData.width
    canvas.height = imageData.height
    const ctx = canvas.getContext('2d')
    if (!ctx) throw new Error('Could not get canvas context')
    ctx.putImageData(imageData, 0, 0)
    return canvas
  }
}
