"use client"

import { useState } from "react"
import Link from "next/link"
import { v4 as uuidv4 } from "uuid"
import { Button } from "@/components/ui/button"
import { PatternCanvas } from "@/components/PatternCanvas"
import { PatternControls } from "@/components/PatternControls"
import { HistorySidebar } from "@/components/HistorySidebar"
import { PatternLayer } from "@/lib/patternGenerator"

interface Pattern {
  id: string
  imageUrl: string
  layers: PatternLayer[]
}

export default function PatternGenerator() {
  const [currentLayers, setCurrentLayers] = useState<PatternLayer[]>([{
    id: uuidv4(),
    shape: {
      type: 'circle',
      size: 50,  // Increased size
      rotation: 0,
      spacing: 100,  // Increased spacing
      opacity: 1
    },
    colors: ['#6d28d9', '#f3f4f6'],  // Changed background color for better contrast
    blendMode: 'source-over',
    opacity: 1,
    scale: 1,
    rotation: 0,
    offset: { x: 0, y: 0 }
  }])

  const [patterns, setPatterns] = useState<Pattern[]>([])
  const [currentImageUrl, setCurrentImageUrl] = useState<string>("")

  const handleLayersUpdate = (layers: PatternLayer[]) => {
    setCurrentLayers(layers)
  }

  const handlePatternGenerated = (imageUrl: string) => {
    setCurrentImageUrl(imageUrl)
  }

  const handleDownload = () => {
    // Save to history first
    const newPattern: Pattern = {
      id: uuidv4(),
      imageUrl: currentImageUrl,
      layers: currentLayers,
    }
    setPatterns((prev) => [newPattern, ...prev])

    // Create download link
    const link = document.createElement("a")
    link.download = `pattern-${newPattern.id}.png`
    link.href = currentImageUrl
    document.body.appendChild(link)
    link.click()
    document.body.removeChild(link)
  }

  const handlePatternSelect = (pattern: Pattern) => {
    setCurrentLayers(pattern.layers)
  }

  const handleClearHistory = () => {
    setPatterns([])
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Navigation */}
      <nav className="fixed top-0 w-full bg-white border-b z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <Link href="/" className="text-xl font-bold text-purple-600">
            Pattern Generator
          </Link>
          <Button variant="ghost" asChild>
            <Link href="/">Exit Editor</Link>
          </Button>
        </div>
      </nav>

      {/* Main Content */}
      <main className="min-h-screen bg-gray-50 pt-16">
        <div className="container mx-auto px-4 py-8">
          <div className="grid grid-cols-1 lg:grid-cols-12 gap-8">
            {/* Controls */}
            <div className="lg:col-span-3">
              <div className="sticky top-24">
                <PatternControls
                  onUpdate={handleLayersUpdate}
                  onDownload={handleDownload}
                />
              </div>
            </div>

            {/* Canvas */}
            <div className="lg:col-span-6">
              <div className="sticky top-24 bg-white rounded-lg shadow-sm">
                <PatternCanvas
                  layers={currentLayers}
                  onPatternGenerated={handlePatternGenerated}
                />
              </div>
            </div>

            {/* History */}
            <div className="lg:col-span-3">
              <div className="sticky top-24">
                <HistorySidebar
                  patterns={patterns.map(p => ({
                    id: p.id,
                    imageUrl: p.imageUrl,
                    patternType: p.layers[0].shape.type,
                    primaryColor: p.layers[0].colors[0],
                    secondaryColor: p.layers[0].colors[1],
                    scale: p.layers[0].scale
                  }))}
                  onPatternSelect={(pattern) => {
                    const fullPattern = patterns.find(p => p.id === pattern.id)
                    if (fullPattern) {
                      handlePatternSelect(fullPattern)
                    }
                  }}
                  onClearHistory={handleClearHistory}
                />
              </div>
            </div>
          </div>
        </div>
      </main>
    </div>
  )
}
