"use client"

import React, { useEffect, useRef, useState } from "react"
import { Card } from "@/components/ui/card"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { PatternGenerator, PatternLayer } from "@/lib/patternGenerator"

interface PatternCanvasProps {
  layers: PatternLayer[]
  onPatternGenerated?: (imageUrl: string) => void
}

export function PatternCanvas({
  layers,
  onPatternGenerated,
}: PatternCanvasProps) {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const generatorRef = useRef<PatternGenerator | null>(null)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    const canvas = canvasRef.current
    if (!canvas) return

    // Set fixed canvas size
    canvas.width = 500
    canvas.height = 500

    // Initialize pattern generator if not already done
    if (!generatorRef.current) {
      generatorRef.current = new PatternGenerator(canvas)
    }

    const generatePattern = async () => {
      try {
        if (!generatorRef.current) return

        // Generate pattern with all layers
        await generatorRef.current.generatePattern(layers)

        // Notify parent component of new pattern
        if (onPatternGenerated) {
          onPatternGenerated(canvas.toDataURL())
        }

        setError(null)
      } catch (err) {
        setError("Error generating pattern. Please try again.")
        console.error("Pattern generation error:", err)
      }
    }

    generatePattern()
  }, [layers, onPatternGenerated])

  const handleDrop = async (e: React.DragEvent<HTMLCanvasElement>) => {
    e.preventDefault()
    const file = e.dataTransfer.files[0]
    if (file && file.type.startsWith('image/')) {
      try {
        const imageData = await loadImageData(file)
        // Emit event to parent component to add new layer with image
        if (onPatternGenerated) {
          const canvas = document.createElement('canvas')
          canvas.width = imageData.width
          canvas.height = imageData.height
          const ctx = canvas.getContext('2d')
          if (ctx) {
            ctx.putImageData(imageData, 0, 0)
            onPatternGenerated(canvas.toDataURL())
          }
        }
      } catch (err) {
        setError("Error loading image. Please try again.")
        console.error("Image loading error:", err)
      }
    }
  }

  const handleDragOver = (e: React.DragEvent<HTMLCanvasElement>) => {
    e.preventDefault()
  }

  return (
    <Card className="p-4">
      {error && (
        <Alert variant="destructive" className="mb-4">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}
      <div className="bg-white rounded-lg border overflow-hidden flex items-center justify-center p-4">
        <div className="w-[500px] h-[500px] relative">
          <canvas
            ref={canvasRef}
            width={500}
            height={500}
            className="absolute top-0 left-0 w-full h-full"
            style={{
              imageRendering: 'pixelated',
              backgroundColor: '#fff'
            }}
            onDrop={handleDrop}
            onDragOver={handleDragOver}
          />
        </div>
      </div>
    </Card>
  )
}

async function loadImageData(file: File): Promise<ImageData> {
  return new Promise((resolve, reject) => {
    const reader = new FileReader()
    reader.onload = (e) => {
      const img = new Image()
      img.onload = () => {
        const canvas = document.createElement('canvas')
        canvas.width = img.width
        canvas.height = img.height
        const ctx = canvas.getContext('2d')
        if (!ctx) {
          reject(new Error('Could not get canvas context'))
          return
        }
        ctx.drawImage(img, 0, 0)
        resolve(ctx.getImageData(0, 0, img.width, img.height))
      }
      img.onerror = () => reject(new Error('Could not load image'))
      img.src = e.target?.result as string
    }
    reader.onerror = () => reject(new Error('Could not read file'))
    reader.readAsDataURL(file)
  })
}
